.modal {
  display: none;
  color: $white;

  &--show {
    display: block;
  }
}

.modal__form {
  position: fixed;
  top: 50%;
  left: 50%;
  padding: 3rem;
  width: 490px;
  background-color: $blue;
  margin: auto;
  transform: translateX(-50%) translateY(-50%);
  z-index: 10000;
}

.modal h2 {
  @include title;
  display: inline-block;

  font-size: 1.75rem;
}

.modal__btn {
  position: relative;
  margin-top: -30px;
  margin-right: -30px;
  padding: 0;
  width: 30px;
  height: 30px;
  float: right;
  border: 0;

  &::before,
  &::after {
    position: absolute;
    top: 50%;
    left: 10%;
    content: "";
    width: 25px;
    height: 1px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &::after {
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(-45deg);
  }
}

.modal__form form {
  display: block;
}

.modal__bg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba($black, 0.5);
  outline: 1px solid;
  z-index: 1000;
}
